<template>
	<div id="advertise-stick">
		<public-header title="商家推广"></public-header>
		<div class="content">
			<div class="content-inner">
				<div class="padding-15-lr padding-10-tb c-gray font-14">{{type == 'stick' ? '可选广告位置' : '广告幻灯片位置'}}</div>
				<template v-if="type == 'stick'">
					<van-radio-group v-model="displayorder">
						<van-cell-group>
							<van-cell clickable v-for="(item, index) in advertise.prices" @click="onSelectPosition(index)" :key="'s' + index">
								<div slot="title">置顶 <span class="c-danger">No.{{item.displayorder}}</span></div>
								<van-radio :name="index" />
							</van-cell>
						</van-cell-group>
					</van-radio-group>
					<van-radio-group v-model="day" class="margin-10-t" v-if="advertise.prices && advertise.prices[displayorder]">
						<van-cell-group>
							<van-cell>
								<div slot="title">
									置顶 <span class="c-danger ">No.{{advertise.prices[displayorder].displayorder}}</span>可选价目{{advertise.prices[displayorder].sailed == 1 ? '（已售）' : ''}}
								</div>
							</van-cell>
							<van-cell :title="'购买广告' + feeItem.day + '天' + feeItem.fee + '元'" clickable @click="onSelectPosition(index, 'day')" v-for="(feeItem, index) in advertise.prices[displayorder].fees" :key="index">
								<van-radio :name="index" v-if="advertise.prices[displayorder].sailed == 0" />
							</van-cell>
						</van-cell-group>
					</van-radio-group>
				</template>
				<template v-else>
					<van-cell-group>
						<van-cell :title="page_title + '推广'" />
					</van-cell-group>
					<van-radio-group v-model="day" class="margin-10-t" >
						<van-cell-group>
							<van-cell>
								<div slot="title">
									广告位总数{{advertise.total}}个 {{advertise.leave > 0 ? '剩余' + advertise.leave + '个位置' : '（已售罄）'}} 价目:
								</div>
							</van-cell>
							<van-cell :title="'购买广告' + feeItem.day + '天' + feeItem.fee + '元'" clickable @click="onSelectPosition(index)" v-for="(feeItem, index) in advertise.prices" :key="index">
								<van-radio :name="index" v-if="advertise.leave > 0" />
							</van-cell>
						</van-cell-group>
					</van-radio-group>
				</template>
				<div class="padding-15-lr padding-10-tb c-gray font-14">选择支付方式</div>
				<van-radio-group v-model="pay_type">
					<van-cell-group>
						<van-cell clickable @click="pay_type = 'credit'" class="no-fix">
							<div slot="title">
								<span class="margin-5-r">余额支付</span>
								<span>账户余额: ¥{{amount}}</span>
							</div>
							<van-radio name="credit" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
			</div>
			<div class="fix-bottom van-hairline--top">
				<van-button size="normal" block class="bg-info" @click="onSubmit()" v-if="advertise.leave">立即购买</van-button>
				<van-button size="normal" block class="van-button--disabled" v-else>此广告位已售罄，去看看其他广告位</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				type: 'slideHomeTop',
				page_title: '',
				advertise: {},
				amount: 0,
				displayorder: 0,
				day: 0,
				pay_type: 'credit'
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/advertise/index/stick',
					data: {
						type: that.type
					},
					success: (res) => {
						that.page_title = res.page_title;
						that.advertise = res.advertise;
						that.amount = res.amount;
						if(that.type == 'stick') {
							for(var i in that.advertise.prices) {
								if(that.advertise.prices[i].sailed == 0) {
									for(var day in that.advertise.prices[i].fees) {
										that.day = day;
										break;
									}
									that.displayorder = i;
									break;
								}
							}
						} else {
							if(that.advertise.leave > 0) {
								for(var day in that.advertise.prices) {
									that.day = day;
									break;
								}
							}
						}
					}
				});
			},
			onSelectPosition(index, type) {
				var that = this;
				if(that.type == 'stick') {
					if(type == 'day') {
						var priceSelect = that.advertise.prices[that.displayorder];
						if(priceSelect.sailed == 1) {
							return false;
						}
						that.day = priceSelect.fees[index].day;
					} else {
						that.displayorder = index;
					}
				} else {
					if(that.advertise.leave > 0) {
						that.day = index;
					}
				}
			},
			onSubmit() {
				var that = this;
				if(that.type == 'stick' && that.displayorder == 0) {
					that.util.$toast('请选择置顶位置', '', 1000);
					return false;
				}
				if(that.day == 0) {
					that.util.$toast('请选择购买天数', '', 1000);
					return false;
				}
				if(that.type == 'stick' && that.advertise.prices[that.displayorder].sailed == 1) {
					that.util.$toast('该位置已售出', '', 1000);
					return false;
				}
				setData({
					vue: that,
					url: 'manage/advertise/index/stick',
					data: {
						type: that.type,
						displayorder: that.displayorder,
						day: that.day,
						pay_type: that.pay_type
					},
					success: function(res) {
						that.util.pay({
							pay_type: that.pay_type,
							order_type: 'advertise',
							order_id: res.id,
							sid: res.sid,
							vue: that
						});
					}
				});
			}
		},
		mounted(){
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.type) {
				this.type = this.query.type;
			}
		}
	}
</script>

<style>
	#advertise-stick .content .content-inner{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 64px;
		right: 0;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#advertise-stick .fix-bottom{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 10px 15px;
		background-color: #fff;
	}
</style>


